"use client";

import { useState, useEffect } from 'react';
import { useTimer } from '@/hooks/useTimer';
import { 
  getSessions, 
  saveSession, 
  clearSessions, 
  getTotalDuration, 
  formatDuration, 
  formatDate,
  StorageError,
  type Session 
} from '@/lib/trackerStorage';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Separator } from '@/components/ui/separator';
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { Calendar } from '@/components/ui/calendar';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';

export default function TrackerPage() {
  const { time, isActive, startTimer, stopTimer, resetTimer, formatTime } = useTimer();
  const [sessions, setSessions] = useState<Session[]>([]);
  const [error, setError] = useState<string | null>(null);
  const [totalDuration, setTotalDuration] = useState<number>(0);
  const [showManualStart, setShowManualStart] = useState<boolean>(false);
  const [selectedDate, setSelectedDate] = useState<Date | undefined>(new Date());
  const [selectedTime, setSelectedTime] = useState<string>('');

  // Load sessions on component mount
  useEffect(() => {
    try {
      const loadedSessions = getSessions();
      setSessions(loadedSessions);
      setTotalDuration(getTotalDuration());
      setError(null);
    } catch (err) {
      if (err instanceof StorageError) {
        setError(err.message);
      } else {
        setError('Ein unbekannter Fehler ist aufgetreten');
      }
    }
  }, []);

  const handleStartSession = () => {
    try {
      startTimer();
      setError(null);
    } catch (err) {
      setError('Timer konnte nicht gestartet werden');
    }
  };

  const handleStopSession = () => {
    try {
      const result = stopTimer();
      
      if (result.duration > 0) {
        const newSession: Session = {
          id: Date.now().toString(),
          startTime: result.actualStartTime,
          duration: result.duration,
          endTime: Date.now()
        };

        saveSession(newSession);
        
        // Update local state
        const updatedSessions = getSessions();
        setSessions(updatedSessions);
        setTotalDuration(getTotalDuration());
      }
      
      resetTimer();
      setError(null);
    } catch (err) {
      if (err instanceof StorageError) {
        setError(err.message);
      } else {
        setError('Sitzung konnte nicht gespeichert werden');
      }
    }
  };

  const handleManualStart = () => {
    try {
      if (selectedDate && selectedTime) {
        const [hours, minutes] = selectedTime.split(':').map(Number);
        const startDateTime = new Date(selectedDate);
        startDateTime.setHours(hours, minutes, 0, 0);
        
        if (startDateTime.getTime() > Date.now()) {
          setError('Startzeit kann nicht in der Zukunft liegen');
          return;
        }
        
        startTimer(startDateTime);
        setShowManualStart(false);
        setError(null);
      } else {
        setError('Bitte wählen Sie Datum und Uhrzeit aus');
      }
    } catch (err) {
      setError('Timer konnte nicht mit benutzerdefinierter Zeit gestartet werden');
    }
  };

  const getCurrentTimeString = () => {
    const now = new Date();
    return `${now.getHours().toString().padStart(2, '0')}:${now.getMinutes().toString().padStart(2, '0')}`;
  };

  // Initialize time input with current time
  useEffect(() => {
    if (!selectedTime) {
      setSelectedTime(getCurrentTimeString());
    }
  }, [selectedTime]);

  const handleClearHistory = () => {
    try {
      clearSessions();
      setSessions([]);
      setTotalDuration(0);
      setError(null);
    } catch (err) {
      if (err instanceof StorageError) {
        setError(err.message);
      } else {
        setError('Historie konnte nicht gelöscht werden');
      }
    }
  };

  return (
    <div className="container mx-auto px-4 py-8 max-w-4xl">
      {/* Header */}
      <div className="text-center mb-8">
        <h1 className="text-4xl font-bold mb-2">Keuschheitskäfig Tracker</h1>
        <p className="text-muted-foreground">Diskrete Zeiterfassung für persönliche Zwecke</p>
      </div>

      {/* Error Display */}
      {error && (
        <Alert className="mb-6 border-destructive">
          <AlertDescription className="text-destructive">
            {error}
          </AlertDescription>
        </Alert>
      )}

      {/* Main Timer Card */}
      <Card className="mb-8">
        <CardHeader className="text-center">
          <CardTitle className="text-2xl">Aktuelle Sitzung</CardTitle>
          <CardDescription>
            {isActive ? 'Timer läuft...' : 'Bereit zum Starten'}
          </CardDescription>
        </CardHeader>
        <CardContent className="text-center">
          <div className="text-6xl font-mono font-bold mb-8 text-primary">
            {formatTime(time)}
          </div>
          
          <div className="flex gap-4 justify-center flex-wrap">
            <Button 
              onClick={handleStartSession}
              disabled={isActive}
              size="lg"
              className="px-8"
            >
              {isActive ? 'Läuft...' : 'Jetzt starten'}
            </Button>
            
            <Dialog open={showManualStart} onOpenChange={setShowManualStart}>
              <DialogTrigger asChild>
                <Button 
                  disabled={isActive}
                  variant="outline"
                  size="lg"
                  className="px-8"
                >
                  Manuell starten
                </Button>
              </DialogTrigger>
              <DialogContent className="sm:max-w-[425px]">
                <DialogHeader>
                  <DialogTitle>Startzeit auswählen</DialogTitle>
                  <DialogDescription>
                    Wählen Sie das Datum und die Uhrzeit, wann die Sitzung begonnen hat.
                  </DialogDescription>
                </DialogHeader>
                <div className="grid gap-4 py-4">
                  <div className="space-y-2">
                    <Label htmlFor="date">Datum</Label>
                    <Popover>
                      <PopoverTrigger asChild>
                        <Button
                          variant="outline"
                          className="w-full justify-start text-left font-normal"
                        >
                          {selectedDate ? selectedDate.toLocaleDateString('de-DE') : 'Datum auswählen'}
                        </Button>
                      </PopoverTrigger>
                      <PopoverContent className="w-auto p-0" align="start">
                        <Calendar
                          mode="single"
                          selected={selectedDate}
                          onSelect={setSelectedDate}
                          disabled={(date) => date > new Date()}
                          initialFocus
                        />
                      </PopoverContent>
                    </Popover>
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="time">Uhrzeit</Label>
                    <Input
                      id="time"
                      type="time"
                      value={selectedTime}
                      onChange={(e) => setSelectedTime(e.target.value)}
                      className="w-full"
                    />
                  </div>
                </div>
                <div className="flex gap-2 justify-end">
                  <Button variant="outline" onClick={() => setShowManualStart(false)}>
                    Abbrechen
                  </Button>
                  <Button onClick={handleManualStart}>
                    Sitzung starten
                  </Button>
                </div>
              </DialogContent>
            </Dialog>
            
            <Button 
              onClick={handleStopSession}
              disabled={!isActive}
              variant="outline"
              size="lg"
              className="px-8"
            >
              Sitzung beenden
            </Button>
          </div>
        </CardContent>
      </Card>

      {/* Statistics */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-8">
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Gesamtstatistik</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-2">
              <div className="flex justify-between">
                <span className="text-muted-foreground">Anzahl Sitzungen:</span>
                <span className="font-semibold">{sessions.length}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-muted-foreground">Gesamtdauer:</span>
                <span className="font-semibold">{formatDuration(totalDuration)}</span>
              </div>
              {sessions.length > 0 && (
                <div className="flex justify-between">
                  <span className="text-muted-foreground">Durchschnitt:</span>
                  <span className="font-semibold">
                    {formatDuration(Math.floor(totalDuration / sessions.length))}
                  </span>
                </div>
              )}
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Aktionen</CardTitle>
          </CardHeader>
          <CardContent>
            <Button 
              onClick={handleClearHistory}
              variant="destructive"
              className="w-full"
              disabled={sessions.length === 0}
            >
              Historie löschen
            </Button>
            <p className="text-xs text-muted-foreground mt-2 text-center">
              Alle gespeicherten Sitzungen werden permanent gelöscht
            </p>
          </CardContent>
        </Card>
      </div>

      {/* Session History */}
      <Card>
        <CardHeader>
          <CardTitle className="text-xl">Sitzungshistorie</CardTitle>
          <CardDescription>
            {sessions.length === 0 
              ? 'Noch keine Sitzungen aufgezeichnet' 
              : `${sessions.length} Sitzung${sessions.length !== 1 ? 'en' : ''} gespeichert`
            }
          </CardDescription>
        </CardHeader>
        <CardContent>
          {sessions.length === 0 ? (
            <div className="text-center py-8 text-muted-foreground">
              <p>Starte deine erste Sitzung, um die Historie zu sehen.</p>
            </div>
          ) : (
            <div className="space-y-3">
              {sessions
                .sort((a, b) => b.endTime - a.endTime)
                .map((session, index) => (
                  <div key={session.id}>
                    <div className="flex justify-between items-center py-3">
                      <div>
                        <p className="font-medium">
                          {formatDate(session.startTime)}
                        </p>
                        <p className="text-sm text-muted-foreground">
                          Sitzung #{sessions.length - index}
                        </p>
                      </div>
                      <div className="text-right">
                        <p className="font-semibold text-lg">
                          {formatDuration(session.duration)}
                        </p>
                        <p className="text-xs text-muted-foreground">
                          bis {formatDate(session.endTime)}
                        </p>
                      </div>
                    </div>
                    {index < sessions.length - 1 && <Separator />}
                  </div>
                ))}
            </div>
          )}
        </CardContent>
      </Card>

      {/* Footer */}
      <div className="text-center mt-8 text-sm text-muted-foreground">
        <p>Alle Daten werden lokal in Ihrem Browser gespeichert.</p>
        <p>Keine Daten werden an externe Server übertragen.</p>
      </div>
    </div>
  );
}
